<template>
  <div>
    <div class="app">
      <f-header :class="{'navBarWrap':navBarFixed}"></f-header>
      <el-row type="flex" justify="center" id="content">
        <el-col :xs="20" :md="20" :style="{'minHeight':minHeight+'px'}">
          <router-view></router-view>
        </el-col>
      </el-row>
      <f-footer></f-footer>
      <el-backtop></el-backtop>
    </div>
  </div>
</template>

<script>
import fHeader from "components/header";
import fFooter from "components/footer";

export default {
  name: "app",
  mounted() {
    let that = this;
    that.minHeight = document.documentElement.clientHeight;
    window.addEventListener("scroll", that.watchScroll);
    window.onresize = function() {
      that.minHeight = document.documentElement.clientHeight;
    };
  },
  data() {
    return {
      minHeight: 370,
      navBarFixed: false
    };
  },
  components: {
    fHeader,
    fFooter
  },
  methods: {
    watchScroll() {
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      //  当滚动超过 50 时，实现吸顶效果
      if (scrollTop > 50) {
        this.navBarFixed = true;
      } else {
        this.navBarFixed = false;
      }
    }
  }
};
</script>

<style lang="scss">
.app {
  font-family: "microsoft yahei";
}
#content {
  background-color: #f9f9f9;
  padding: 30px 0;
}
.navBarWrap {
  position: fixed;
  top: 0;
  z-index: 999;
  width: 100%;
}
</style>
