import Vue from 'vue'
import VueRouter from 'vue-router'

const Home = () => import('views/Home')
const Archive = () => import('views/Archive')
const Article = () => import('views/Article')

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Home
  },
  {
    path: '/home',
    name: 'home',
    component: Home
  },
  {
    path: '/archive',
    name: 'archive',
    component: Archive
  },
  {
    path: '/article/:id',
    name: 'article',
    component: Article
  },
  {
    path: '/tag/:id',
    name: 'tag',
    component: Archive
  },
  {
    path: '*',
    redirect: '/'
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
